import * as CryptoJS from 'crypto-js';

export enum EncryptType {
    AES_128 = 16,
    AES_256 = 32
}

export class EncryptHelper {
    constructor() {}

    GenKey(len: number) {
        let key = '';
        const charList = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=';
        for (let r = 0; r < len; r++) {
            const _char = Math.random() * charList.length;
            const _char2 = Math.floor(_char);
            const _char3 = charList.charAt(_char2);
            key += _char3;
        }
        return key;
    }

    ByteCount(s: string) {
        return encodeURI(s).split(/%..|./).length - 1;
    }

    /**
     * 加密
     * @param plaintText
     * @param encryptType
     * @returns
     */
    Encrypt(plaintText: string, encryptType: EncryptType = EncryptType.AES_128): string {
        let secretText: string = '';
        // key & iv - 128-bit (16 byte) / 256-bit (32 byte)
        const AES_KEY = this.GenKey(encryptType);
        //console.log(this.ByteCount(AES_KEY));
        // AES-128
        try {
            const Utf8_plaintText = CryptoJS.enc.Utf8.parse(plaintText);
            const Utf8_key = CryptoJS.enc.Utf8.parse(AES_KEY);
            const Utf8_iv = CryptoJS.enc.Utf8.parse(AES_KEY.substring(0, 16));

            const encrypted = CryptoJS.AES.encrypt(Utf8_plaintText, Utf8_key, {
                iv: Utf8_iv,
                mode: CryptoJS.mode.CBC,
                padding: CryptoJS.pad.Pkcs7
            });
            secretText = `${AES_KEY}${encrypted.toString()}`;
        } catch (e) {
            secretText = '';
        }
        return secretText;
    }

    /**
     * 解密
     * @param cipherText
     * @param encryptType
     * @returns
     */
    Decrypt(cipherText: String, encryptType: EncryptType = EncryptType.AES_128): string {
        try {
            const AES_KEY = cipherText.substring(0, encryptType);
            const Utf8_key = CryptoJS.enc.Utf8.parse(AES_KEY);
            const Utf8_iv = CryptoJS.enc.Utf8.parse(AES_KEY.substring(0, 16));
            const encryptedBase64CipherText = cipherText.substring(encryptType);

            const decrypted = CryptoJS.AES.decrypt(encryptedBase64CipherText, Utf8_key, {
                iv: Utf8_iv,
                mode: CryptoJS.mode.CBC,
                padding: CryptoJS.pad.Pkcs7
            });
            return CryptoJS.enc.Utf8.stringify(decrypted).toString();
        } catch (e) {
            return '';
        }
    }
}
