// 加密邏輯
import CryptoJS from 'crypto-js';
import JSEncrypt from 'jsencrypt';
import * as Common from '@/libs/Common';

export function encryptData(jsonData: string, rsaPublicKey: string) {
    const aesKey = CryptoJS.lib.WordArray.random(32); // AES-256 (32 bytes)
    const iv = CryptoJS.lib.WordArray.random(16); // IV

    // AES 加密帳密
    const encrypted = CryptoJS.AES.encrypt(jsonData, aesKey, {
        iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
    }).toString();

    // AES 金鑰轉 hex 並使用 RSA 加密
    const aesKeyHex = aesKey.toString(CryptoJS.enc.Hex);

    const encryptor = new JSEncrypt();
    encryptor.setPublicKey(rsaPublicKey);
    const encryptedAesKey = encryptor.encrypt(aesKeyHex);

    Common.ConsoleLog('AES Hex Length:' + aesKeyHex.length); // 應為 64
    const key = encryptor.getKey();
    if (key) {
        // Since 'n' is protected, avoid accessing it directly.
        // Instead, use the public key string length as an approximate indicator.
        const publicKey = encryptor.getPublicKey();
        // The getPublicKey() method may return null or cause errors if key is not fully initialized.
        // To avoid errors, check if publicKey is a non-empty string before using it.
        if (typeof publicKey === 'string' && publicKey.length > 0) {
            const rsaKeySize = publicKey.length * 8; // length in bits approx.
            Common.ConsoleLog('RSA Key Size (approx):' + rsaKeySize); // 要 >= 2048
        } else {
            Common.ConsoleLog('RSA Key Size: public key not available or invalid');
        }
    } else {
        Common.ConsoleLog('RSA Key Size: key not available');
    }

    if (!encryptedAesKey) {
        throw new Error('RSA encryption of AES key failed');
    }

    return {
        aesEncryptedData: encrypted,
        encryptedAesKey,
        base64Iv: CryptoJS.enc.Base64.stringify(iv)
    };
}
