<template>
    <div id="OuterContainer" class="d-flex justify-content-center align-items-center bg-light" data-bs-theme="light">
        <div class="login-container bg-white text-dark">
            <h3 class="text-center mb-4">電子檔案管理系統登入</h3>
            <div>
                <div class="mb-3">
                    <label for="username" class="form-label">帳號</label>
                    <input
                        v-model="loginData.AccountID.value"
                        autocomplete="off"
                        v-on:focus="RestoreAccount()"
                        maxlength="20"
                        type="text"
                        class="form-control"
                        id="username"
                        placeholder="輸入帳號"
                        required
                    />
                    <!-- error message -->
                    <div
                        v-if="loginData.AccountID.errorMsg"
                        class="inputAlert"
                        v-bind:class="{ 'd-block': loginData.AccountID.errorMsg !== '' }"
                    >
                        <span class="input-warning-text field-validation-valid">{{
                            loginData.AccountID.errorMsg
                        }}</span>
                    </div>
                    <!-- error message -->
                </div>
                <div class="mb-3">
                    <label for="password" class="form-label">密碼</label>
                    <input
                        v-model="loginData.Password.value"
                        autocomplete="off"
                        maxlength="20"
                        type="password"
                        class="form-control"
                        id="password"
                        placeholder="輸入密碼"
                        required
                    />
                    <!-- error message -->
                    <div
                        v-if="loginData.Password.errorMsg"
                        class="inputAlert"
                        v-bind:class="{ 'd-block': loginData.Password.errorMsg !== '' }"
                    >
                        <span class="input-warning-text field-validation-valid">{{ loginData.Password.errorMsg }}</span>
                    </div>
                    <!-- error message -->
                </div>

                <!-- 驗證碼區塊 -->
                <div class="mb-3">
                    <label for="captcha" class="form-label">驗證碼</label>
                    <div class="input-group">
                        <input
                            ref="CaptchaCode"
                            v-cloak
                            v-on:keydown="GoEnterKey($event)"
                            v-model="loginData.CaptchaCode.value"
                            maxlength="4"
                            type="text"
                            class="form-control"
                            id="captcha"
                            placeholder="輸入驗證碼"
                            required
                        />
                        <img
                            v-bind:src="loginData.CaptchaImgSrc"
                            v-on:click="RefreshCaptcha"
                            alt="驗證碼"
                            id="captchaImage"
                            class=""
                        />
                    </div>
                    <!-- error message -->
                    <div
                        v-if="loginData.CaptchaCode.errorMsg"
                        class="inputAlert"
                        v-bind:class="{ 'd-block': loginData.CaptchaCode.errorMsg !== '' }"
                    >
                        <span class="input-warning-text field-validation-valid">{{
                            loginData.CaptchaCode.errorMsg
                        }}</span>
                    </div>
                    <!-- error message -->
                </div>
                <div class="mb-3 form-check">
                    <input
                        type="checkbox"
                        class="form-check-input"
                        id="rememberMe"
                        v-model="loginData.RememberID"
                        value="Y"
                    />
                    <label class="form-check-label" for="rememberMe">記住帳號</label>
                </div>
                <a v-on:click="Go($event)" class="btn btn-esun-primary w-100">登入</a>
            </div>
            <!-- <div  class="text-center mt-3">
                <button v-on:click="emit('toggleTheme')" class="btn btn-esun-outline btn-sm">切換深色模式</button>
            </div>-->
        </div>
    </div>
</template>

<script setup lang="ts">
import * as ApiClient from '@/libs/ApiClient';
import API from '@/api/v1/1_LoginPage';
import { RouteUrlList } from '@/libs/RouteList';
import * as Common from '@/libs/Common';
import _ from 'lodash';
import type { IUserInput, IPrivacyUserInput } from '@/models/DTO/CommonModels';
import { LocalStorageKey } from '@/models/LocalStorageKey';
import { EncryptHelper, EncryptType } from '@/libs/EncryptHelper';
import isMobile from 'ismobilejs';

import { SimplePopUp, SimplePopUpBtnType, SimplePopUpModalType, SimplePopUpSize } from '@/libs/SimplePopUp';
import { nextTick, onMounted, onUnmounted, reactive, ref, watch } from 'vue';
import { useRouter } from 'vue-router';
import { encryptData } from '@/libs/Crypto';

const simplePopUp = new SimplePopUp();
const router = useRouter();
const _EncryptHelper = new EncryptHelper();

declare interface ILoginData {
    IsUnmounted: boolean;
    IsMobile: boolean;
    IsLogin: boolean;
    rsaPublicKey: string;
    AccountID: IPrivacyUserInput<string>;

    RememberID: string | null | undefined;
    Password: IUserInput<string>;

    CaptchaImgSrc: string;
    CaptchaCode: IUserInput<string>;
}
const loginData = reactive<ILoginData>({
    IsUnmounted: false,
    IsMobile: isMobile(window.navigator).any,
    IsLogin: false,
    rsaPublicKey: '',
    AccountID: { mask: '', value: '', errorMsg: '' },
    Password: { value: '', errorMsg: '' },
    RememberID: '',

    CaptchaImgSrc: '',
    CaptchaCode: { value: '', errorMsg: '' }
});

const emit = defineEmits(['toggleTheme']);

// Methods
const LoadViewData = async () => {
    try {
        const data = await API.LoginViewData();

        if (loginData.IsUnmounted) return;

        if (data.status) {
            loginData.rsaPublicKey = data.rsaPublicKey;
            loginData.CaptchaImgSrc = data.captchaCodeBase64Src;
        } else {
            ApiClient.HandleGeneralRespError(data);
        }
    } catch (e) {
        if (!loginData.IsUnmounted) {
            ApiClient.HandleError(e);
        }
        Common.ConsoleLog(e);
    }
};

// 檢查 前端參數
const IsSendAble = (isDisplayError = false) => {
    let FullErrorMsg = '';

    if (!loginData.AccountID.value) {
        let err = '請填寫帳號\n';
        FullErrorMsg += err;
        loginData.AccountID.errorMsg = isDisplayError ? err : '';
    } else if (!/^[A-Za-z0-9]{1,20}$/.test(loginData.AccountID.value)) {
        let err = '帳號格式錯誤\n';
        FullErrorMsg += err;
        loginData.AccountID.errorMsg = isDisplayError ? err : '';
    }

    loginData.Password.errorMsg = '';
    if (!loginData.Password.value) {
        let err = '請填寫密碼\n';
        FullErrorMsg += err;
        loginData.Password.errorMsg = isDisplayError ? err : '';
    } else if (!Common.GeneralAllowInput(loginData.Password.value, 1, 10)) {
        let err = '密碼格式錯誤\n';
        FullErrorMsg += err;
        loginData.Password.errorMsg = isDisplayError ? err : '';
    }

    if (!/^\d{4}$/.test(loginData.CaptchaCode.value)) {
        let err = '驗證碼格式錯誤\n';
        FullErrorMsg += err;
        loginData.CaptchaCode.errorMsg = isDisplayError ? err : '';
    }

    if (isDisplayError) {
        FocusToErrorElement();
    }
    return FullErrorMsg === '';
};

const FocusToErrorElement = (): void => {
    nextTick(() => {
        if (loginData.AccountID.errorMsg) {
            // (this.$refs.SSN as HTMLElement).focus(); //TODO: Need to find a way to access ref in composable
        } else if (loginData.CaptchaCode.errorMsg) {
            // (this.$refs.CaptchaCode as HTMLElement).focus(); //TODO: Need to find a way to access ref in composable
        }
    });
};

/**
 *@todo 重新整理 驗證碼
 */
const RefreshCaptcha = () => {
    LoadViewData();
};
/**
 * Hybrid 加密登入資料
 * @param account 帳號
 * @param password 密碼
 * @param rsaPublicKey RSA 公鑰（PEM格式）
 */
const encryptLoginData = (
    account: string,
    password: string,
    rsaPublicKey: string
): {
    aesEncryptedData: string;
    encryptedAesKey: string;
    base64Iv: string;
} => {
    const data = JSON.stringify({ account, password });
    return encryptData(data, rsaPublicKey);
};

const RestoreAccount = () => {
    loginData.AccountID.mask = loginData.AccountID.value;
};

const GoEnterKey = (event: KeyboardEvent) => {
    if (event.key === 'Enter') {
        event.preventDefault(); // Prevent the default form submission (optional)
        Go(event);
    }
};

const Go = async (event: Event) => {
    try {
        if (!(event.target instanceof Element)) {
            return;
        }
        let target: HTMLBodyElement | null = event.currentTarget as HTMLBodyElement;

        // 前端參數檢查
        if (!IsSendAble(true)) {
            return;
        }
        // 加密帳號密碼
        const encryptData = encryptLoginData(
            loginData.AccountID.value,
            loginData.Password.value,
            loginData.rsaPublicKey
        );
        const postData = {
            captchaCode: loginData.CaptchaCode.value,
            aesEncryptedData: encryptData.aesEncryptedData,
            encryptedAesKey: encryptData.encryptedAesKey,
            base64Iv: encryptData.base64Iv
        };
        API.LoginPostBack(postData)
            .then((res: ApiClient.GeneralResp) => {
                // 記住身份證
                if (loginData.RememberID === 'Y') {
                    localStorage.setItem(
                        LocalStorageKey.AccountID,
                        _EncryptHelper.Encrypt(loginData.AccountID.value.trim(), EncryptType.AES_256)
                    );
                } else {
                    localStorage.removeItem(LocalStorageKey.AccountID);
                }

                if (res.status) {
                    router.replace(RouteUrlList.Index.path);
                } else {
                    loginData.CaptchaCode.value = '';
                    RefreshCaptcha();
                    ApiClient.HandleGeneralRespError(res);
                }
            })
            .catch((error) => {
                ApiClient.HandleError(error);
            });
    } catch (e) {
        Common.ConsoleLog(e);
    }
};

onUnmounted(() => {
    loginData.IsUnmounted = true;
});

const DeviceDetect = (): void => {
    loginData.IsMobile = isMobile(window.navigator).any;
};

const MaskAccountID = () => {
    loginData.AccountID.value = loginData.AccountID.mask;
    if (loginData.AccountID.value && loginData.AccountID.value.length > 4) {
        loginData.AccountID.mask =
            loginData.AccountID.value.substring(0, loginData.AccountID.value.length - 4) + '****';
    }
};
onMounted(() => {
    // 確保畫面渲染完 //TODO: remove this, handled in component
    emit('toggleTheme');
    // if (isUnmounted.value) return; //TODO: remove this, handled in component

    DeviceDetect();
    LoadViewData();
    // if (isUnmounted.value) return; //TODO: remove this, handled in component

    // load value from LocalStorage
    const _AccountID = localStorage.getItem(LocalStorageKey.AccountID) || '';
    if (_AccountID) {
        loginData.AccountID.value = _EncryptHelper.Decrypt(_AccountID, EncryptType.AES_256);
        loginData.AccountID.mask = loginData.AccountID.value;
        MaskAccountID();
        loginData.RememberID = 'Y';
    }
});

// 監聽縣市變化
watch(
    () => [loginData.AccountID.value, loginData.Password.value, loginData.CaptchaCode.value],
    ([AccountID, Password, CaptchaCode]) => {
        IsSendAble(false);
    }
);
</script>

<style scoped>
#OuterContainer {
    height: 100vh;
}

.login-container {
    max-width: 400px;
    padding: 2rem;
    border-radius: 12px;
    border: 0.5px solid #4d9aad; /* 主色調調暗柔和一點 */
    background-color: #f9fcfe; /* 較柔和的淡藍白 */
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5); /* 陰影更淡 */
    transition:
        border-color 0.3s ease,
        box-shadow 0.3s ease;
}

/* 滑鼠懸停或聚焦時有更明顯的高亮 */
.login-container:hover,
.login-container:focus-within {
    border-color: #4d9aad; /* 更深的柔和藍 */
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.5);
}

/* 深色模式下調整背景、邊框與陰影 */
[data-bs-theme='dark'] .login-container {
    background-color: #14252b; /* 深色背景 */
    border: 2px solid #3a6a7c; /* 柔和深藍邊框 */
    box-shadow: 0 4px 20px rgba(58, 106, 124, 0.25);
}

[data-bs-theme='dark'] .login-container:hover,
[data-bs-theme='dark'] .login-container:focus-within {
    border-color: #5a8ca3; /* 淡藍高亮 */
    box-shadow: 0 6px 24px rgba(90, 140, 163, 0.35);
}

.form-control {
    border: 2px solid #7ab9c9; /* 主色較柔和 */
    border-radius: 8px;
    transition:
        border-color 0.3s ease,
        box-shadow 0.3s ease;
    background-color: #f0f7fa; /* 輕柔淡藍背景 */
    color: #15373f; /* 字體更深，增加對比，方便閱讀 */
}

.form-control::placeholder {
    color: #307082; /* 較淺的提示文字 */
}

.form-control:focus {
    border-color: #5a9db0;
    box-shadow: 0 0 0 0.2rem rgba(90, 157, 176, 0.3);
    outline: none;
}

/* 深色主題下調整 input 邊框顏色 */
[data-bs-theme='dark'] .form-control {
    background-color: #1a2c34; /* 深色輸入框背景 */
    color: #d4e1e6; /* 字體加深，提高對比 */
    border: 2px solid #3a6a7c;
}

[data-bs-theme='dark'] .form-control::placeholder {
    color: #86bdd6; /* 提示文字淡藍 */
}

[data-bs-theme='dark'] .form-control:focus {
    border-color: #5a8ca3;
    box-shadow: 0 0 0 0.2rem rgba(90, 140, 163, 0.3);
}
</style>
